// dropdown main menu
.dropdown-menu {
    margin-top: 8px;
    border: 1px solid #ebebeb;
    border-radius: 0;
    width: 280px;
    .dropdown-item {
        display: block;
        font-size: 1rem;
        color: $ok-gray!important;
        white-space: normal;
        line-height: 1;
        padding: .75rem 1rem;
        &:hover {
            color: $ok-dark-gray!important;
            text-decoration: none;
        }
    }
}

.dropdown-submenu {
    position: relative;
    &>a:after {
        content: ">";
        color: $ok-gray;
        float: right;
    }
    &>.dropdown-menu {
        top: 0;
        left: 100%;
        margin-top: 0px;
        margin-left: 0px;
    }
    &:hover>.dropdown-menu {
        display: block;
    }
    .dropdown-menu {
        .mega-menu {
            .mega-menu-items {
                a {
                    display: block;
                    font-size: 1rem;
                    line-height: 1;
                    color: $ok-gray;
                    padding: .5rem 1.5rem .5rem 0;
                    &:hover {
                        color: $ok-dark-gray;
                        text-decoration: none;
                    }
                }
            }
            .mega-menu-brands {
                a {
                    display: block;
                    font-size: 1rem;
                    line-height: 1;
                    color: $ok-gray;
                    padding: .5rem 1.5rem .5rem 0;
                    &:hover {
                        color: $ok-dark-gray;
                        text-decoration: none;
                    }
                }
            }
        }
    }
}


/* https://github.com/Mango/slideout */

.slideout-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 256px;
    min-height: 100vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 0;
    display: none;
}

.slideout-menu {
    &-left {
        left: 0;
    }
    &-right {
        right: 0;
    }
}

.slideout-panel {
    position: relative;
    z-index: 1;
    // will-change: transform;
    background-color: #fff;
    /* A background-color is required */
    min-height: 100vh;
}

.slideout-open {
    &,
    body,
    .slideout-panel {
        overflow: hidden;
    }
}

.slideout-open .slideout-menu {
    display: block;
}

.slideout-panel {
    &:before {
        content: '';
        display: block;
        background-color: rgba(0, 0, 0, 0);
        transition: background-color 0.5s ease-in-out;
    }
}

.panel-open {
    &:before {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, .5);
        z-index: 1050;
    }
}

#offcanvas-menu {
    .nav-link {
        color: $ok-dark-gray;
        font-size: 1.125rem;
        line-height: 1;
        &[data-toggle] {
            &:after {
                font-family: 'Font Awesome\ 5 Free';
                font-weight: 900;
                vertical-align: middle;
                margin-left: .5rem;
                color: $ok-blue;
            }
            &.collapsed:after {
                content: "\f107";
            }
            &:not(.collapsed):after {
                content: "\f106";
            }
        }
    }
}


/*sticky header position*/

.sticky {
    &.is-sticky {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1000;
        width: 100%;
        background-color: #fff;
    }
}

