                    table {
                        th {
                            color: $ok-gray;
                            font-weight: normal;
                            // text-align: center;
                        }
                        td {
                            div {
                                color: $ok-dark-gray;
                            }
                            span {
                                color: $ok-gray;
                            }
                            img {
                                min-width: 50px;
                                max-width: 80px;
                                max-height: 50px;
                            }
                        }
                    }
