                        display: inline-block;
                        margin-top: .5rem;
                        line-height: 1;
                        color: $ok-gray;
                    }
                    .product-title {
                        display: block;
                        font-size: 1.125rem;
                        line-height: 1;
                        font-weight: bold;
                        color: $ok-dark-gray;
                        margin-bottom: 1rem;
                        &:hover {
                            text-decoration: none;
                        }
                    }
                    .counter {
                        display: flex;
                        button {
                            width: 30px;
                            height: 30px;
                            color: $ok-dark-gray;
                            background-color: $ok-light-gray;
                            border-color: transparent;
                            border-radius: 50%;
                            &:hover {
                                color: #fff;
                                background-color: $ok-blue;
                            }
                            &:focus {
                                outline: none;
                            }
                        }
                        input {
                            width: 2.5rem;
                            text-align: center;
                            border: none;
                        }
                    }
                }
            }
            .provider-total {
                .total {
                    font-size: 1.125rem;
                    color: $ok-gray;
                    span {
                        color: $ok-dark-gray;
                    }
                    padding: .5rem;
                    border-bottom: 1px solid $ok-light-gray;
                    margin-bottom: 1rem;
                }
                .delivery-block {
                    position: relative;
                    height: 100%;
                    background-color: $ok-light-gray;
                    border: 1px solid $ok-gray;
                    font-size: 0.875rem;
                    padding: .5rem;
                    &:hover {
                        cursor: pointer;
                    }
                    .message {
                        color: $ok-gray;
                        padding-left: 1rem;
                    }
                    &.active {
                        .message {
                            color: $ok-blue;
                        }
                        border: 1px solid $ok-blue;
                    }
                    .form-check-input {
                        position: absolute;
                        top: .4rem;
                        left: 1.5rem;
                    }
                }
            }
        }
    }
}

.total-block {
