// button to top
#button {
    display: none;
    background-color: $ok-gray;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 4px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    transition: background-color .3s;
    z-index: 1000;
    &:hover {
        cursor: pointer;
        background-color: $ok-dark-gray;
    }
    &::after {
        content: '\f077';
        font-family: 'Font Awesome 5 Free';
        font-weight: 900;
        font-style: normal;
        font-size: 2em;
        line-height: 50px;
        color: $ok-blue;
    }
}

// bottom sticky
.sticky-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    width: 100%;
    background-color: #fff;
}
.cart-mini,
.favorite-mini,
.catalog-mini,
.profile-mini {
    display: block;
    position: relative;
    .num-goods {
        position: absolute;
        right: -7px;
        top: -7px;
        background-color: $ok-green;
        color: #000;
        font-size: 0.75rem;
        padding: 0 2px;
        border-radius: 2px;
    }
}

